'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { CheckCircle, Clock, Zap, Activity } from 'lucide-react';
import { Card } from './card';
import { Badge } from './badge';
import { useNetworkStore } from '@/store/network-store';

export const SystemStatus: React.FC = () => {
  const { webSocketConnected, apiConnected, devices, metrics } =
    useNetworkStore();

  const getOverallStatus = () => {
    if (webSocketConnected && apiConnected) {
      return {
        status: 'Real-time Active',
        color: 'text-green-400',
        bg: 'bg-green-500/20 border-green-500/20',
        icon: CheckCircle,
      };
    } else if (apiConnected) {
      return {
        status: 'API Only',
        color: 'text-yellow-400',
        bg: 'bg-yellow-500/20 border-yellow-500/20',
        icon: Clock,
      };
    } else {
      return {
        status: 'Demo Mode',
        color: 'text-red-400',
        bg: 'bg-red-500/20 border-red-500/20',
        icon: Activity,
      };
    }
  };

  const status = getOverallStatus();
  const StatusIcon = status.icon;

  const onlineDevices = devices.filter((d) => d.status === 'online').length;
  const offlineDevices = devices.filter((d) => d.status === 'offline').length;

  return (
    <Card className="border-slate-700 bg-slate-800/50">
      <div className="p-4">
        <div className="mb-4 flex items-center justify-between">
          <h3 className="text-lg font-semibold text-white">–°—Ç–∞—Ç—É—Å —Å–∏—Å—Ç–µ–º—ã</h3>
          <Badge className={status.bg}>
            <StatusIcon className="mr-2 h-4 w-4" />
            {status.status}
          </Badge>
        </div>

        <div className="grid grid-cols-2 gap-4">
          {/* –ü–æ–¥–∫–ª—é—á–µ–Ω–∏—è */}
          <div className="space-y-2">
            <h4 className="text-sm font-medium text-slate-300">–ü–æ–¥–∫–ª—é—á–µ–Ω–∏—è</h4>

            <div className="flex items-center justify-between">
              <span className="text-xs text-slate-400">WebSocket:</span>
              <div className="flex items-center space-x-1">
                <div
                  className={`h-2 w-2 rounded-full ${webSocketConnected ? 'bg-green-400' : 'bg-red-400'}`}
                />
                <span className="text-xs text-white">
                  {webSocketConnected ? '–ê–∫—Ç–∏–≤–µ–Ω' : '–û—Ç–∫–ª—é—á–µ–Ω'}
                </span>
              </div>
            </div>

            <div className="flex items-center justify-between">
              <span className="text-xs text-slate-400">API:</span>
              <div className="flex items-center space-x-1">
                <div
                  className={`h-2 w-2 rounded-full ${apiConnected ? 'bg-green-400' : 'bg-red-400'}`}
                />
                <span className="text-xs text-white">
                  {apiConnected ? '–ü–æ–¥–∫–ª—é—á–µ–Ω' : '–ù–µ–¥–æ—Å—Ç—É–ø–µ–Ω'}
                </span>
              </div>
            </div>
          </div>

          {/* –£—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ */}
          <div className="space-y-2">
            <h4 className="text-sm font-medium text-slate-300">–£—Å—Ç—Ä–æ–π—Å—Ç–≤–∞</h4>

            <div className="flex items-center justify-between">
              <span className="text-xs text-slate-400">–û–Ω–ª–∞–π–Ω:</span>
              <motion.span
                className="text-xs font-medium text-green-400"
                animate={onlineDevices > 0 ? { scale: [1, 1.1, 1] } : {}}
                transition={{ duration: 0.5 }}
              >
                {onlineDevices}
              </motion.span>
            </div>

            <div className="flex items-center justify-between">
              <span className="text-xs text-slate-400">–û—Ñ–ª–∞–π–Ω:</span>
              <span className="text-xs font-medium text-red-400">
                {offlineDevices}
              </span>
            </div>

            <div className="flex items-center justify-between">
              <span className="text-xs text-slate-400">–í—Å–µ–≥–æ:</span>
              <span className="text-xs font-medium text-white">
                {devices.length}
              </span>
            </div>
          </div>
        </div>

        {/* –í–æ–∑–º–æ–∂–Ω–æ—Å—Ç–∏ */}
        <div className="mt-4 border-t border-slate-700 pt-3">
          <div className="grid grid-cols-2 gap-2 text-xs">
            <div className="flex items-center space-x-2">
              <div
                className={`h-2 w-2 rounded-full ${webSocketConnected ? 'animate-pulse bg-green-400' : 'bg-slate-600'}`}
              />
              <span className="text-slate-400">Real-time –ø–∏–Ω–≥</span>
            </div>

            <div className="flex items-center space-x-2">
              <div
                className={`h-2 w-2 rounded-full ${webSocketConnected ? 'animate-pulse bg-blue-400' : 'bg-slate-600'}`}
              />
              <span className="text-slate-400">Live –≥—Ä–∞—Ñ–∏–∫–∏</span>
            </div>

            <div className="flex items-center space-x-2">
              <div
                className={`h-2 w-2 rounded-full ${apiConnected ? 'bg-purple-400' : 'bg-slate-600'}`}
              />
              <span className="text-slate-400">API –∏–Ω—Ç–µ–≥—Ä–∞—Ü–∏—è</span>
            </div>

            <div className="flex items-center space-x-2">
              <div
                className={`h-2 w-2 rounded-full ${webSocketConnected ? 'animate-pulse bg-orange-400' : 'bg-slate-600'}`}
              />
              <span className="text-slate-400">Auto –º–æ–Ω–∏—Ç–æ—Ä–∏–Ω–≥</span>
            </div>
          </div>
        </div>

        {/* –ò–Ω–¥–∏–∫–∞—Ç–æ—Ä –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ */}
        {webSocketConnected && (
          <div className="mt-3 flex items-center justify-center">
            <motion.div
              className="flex items-center space-x-2 text-xs text-green-400"
              animate={{ opacity: [1, 0.6, 1] }}
              transition={{ duration: 2, repeat: Infinity }}
            >
              <Zap className="h-3 w-3" />
              <span>–°–∏—Å—Ç–µ–º–∞ –∞–∫—Ç–∏–≤–Ω–∞</span>
            </motion.div>
          </div>
        )}
      </div>
    </Card>
  );
};
